.movies .movie{   /*class 안의 class*/
    width: 135%; /*하나당 차지하는 비율*/ 
    background-color: white;
    margin-bottom :70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    color:#b2b3bc;
    box-shadow:0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025)

}
.movies a{
    width:40%;
    height:30%;
    text-decoration: none;
    margin-bottom: 30px;
    padding-bottom: 30px;
    margin-right: 10px;
}
.movie img{
    position: relative;
    top : -50px;
    max-width: 100px;
    margin-right: 30px;
    box-shadow:0 13px 27px -5px rgba(50,50,93,.25), 0 8px 16px -8px rgba(0,0,0,.3), 0 -6px 16px -6px rgba(0,0,0,.025)

}

.movie .movie__title,
.movie .movie__year {
    margin:0;
    font-weight: 300;
    
}

.movie .movie__title{
    margin-bottom: 5px;
    font-size:25px;
    color:#2c2c2c;
}
.movie .movie__genres{
    list-style:none;
    padding: 0;
    margin: 0;
    display:flex;
    margin-right:5px;
}

.movie .movie__summary{
    padding: 0;
    margin: 0;
    display:flex;
    margin:5px;
    margin: 5px 0px;
    font-size: 10px;
}
.movie__genres li{
    font-size:14px;
}

.movie {
    transition :0.5s
}
.movie :hover{
    color:#b2b3bc;
}